body {
  margin: 0;
  padding: 0;
  font-family: "Muli", sans-serif;
}

.App {
  text-align: center;
}

.header {
  width: 100%;
  min-height: 10vh;
  background-color: rgba(25, 26, 85, 0.87);
  color: #d9d5cb;
  font-size: 3rem;
}

.header__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 70%;
  margin: 30px auto;
  padding: 20px;
}

.header--icon {
  font-size: 3.5rem;
  margin-right: 20px;
}

.timer__message {
  color: #631414;
  margin: 12px 0;
}

@media (max-width: 1024px) {
  .header__container {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .header {
    font-size: 2.5rem;
  }
  .header--icon {
    font-size: 3rem;
  }
}

@media (max-width: 640px) {
  .header {
    font-size: 1.8rem;
  }
  .header--icon {
    font-size: 2.2rem;
  }
}

.display__container {
  min-width: 100%;
  min-height: 50%;
}

.display--inner {
  margin-left: 21%;
  min-width: 80%;
  min-height: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.display--unit {
  -webkit-flex-basis: content;
          flex-basis: content;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.display--input {
  font-family: "Muli", sans-serif;
  font-size: 2rem;
  text-align: center;
  border-radius: 5px;
}

.display--separate {
  -webkit-flex-basis: content;
          flex-basis: content;
  height: 100%;
  font-family: "Muli", sans-serif;
  font-size: 2rem;
  padding-bottom: 1.5rem;
  margin: 0 2px;
}

.display--countdown {
  width: 75%;
  background-color: black;
  font-weight: bold;
  font-size: 2.2rem;
  letter-spacing: 1.5px;
  border-radius: 5px;
  color: #d9d5cb;
}

@media (max-width: 640px) {
  .display--input,
  .display--separate,
  .display--countdown {
    font-size: 1.6rem;
  }
  .display--countdown {
    font-size: 1.8rem;
  }
}

.controls {
  margin: 0 auto;
  width: 90%;
  min-height: 30%;
  padding-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch;
}

.controls__button {
  font-family: "Muli", sans-serif;
  cursor: pointer;
  opacity: 0.7;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  padding: 10px;
  margin: 0 5px;
  border-radius: 5px;
  color: #d9d5cb;
  font-weight: bold;
  font-size: 1.2rem;
  letter-spacing: 3px;
}

.controls__button:hover {
  opacity: 1;
}

.controls--start {
  background-color: #497527;
}

.controls--stop {
  background-color: #631414;
}

.controls--resume {
  background-color: #497527;
}

.controls--restart {
  background-color: #d08427;
}

.controls--reset {
  background-color: black;
}

@media (max-width: 640px) {
  .controls {
    width: 80%;
  }
  .controls__button {
    font-size: 0.9rem;
  }
}

.timer__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0 auto;
  height: 200px;
  width: 500px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: #75787b;
}

.timer--commands {
  font-size: 1.5rem;
  padding: 10px;
  color: #d9d5cb;
}

@media (max-width: 640px) {
  .timer__container {
    width: 440px;
  }
  .timer--commands {
    font-size: 1.3rem;
  }
}

